









import {
  StepNav,
  Step1,
  Step2,
  Step3,
  Step4,
  Step5,
  Step6
} from "@/components/simulation-step";
import { Component, Vue } from "vue-property-decorator";
import { SimulationStep } from "@/utils/simulation";
import { EventBus } from "@/utils/event-bus";

@Component({
  components: { StepNav, Step1, Step2, Step3, Step4, Step5, Step6 }
})
export default class Simulation extends Vue {
 private step: SimulationStep = SimulationStep.Step1;

  private componentArr = [Step1, Step2, Step3, Step4, Step5, Step6]

  get currentComponent () {
    return this.componentArr[this.step]
  }

  mounted () {
    EventBus.$on('simulationStepChange', (step: SimulationStep) => {
      this.step = step
    })
  }

  beforeDestroy () {
    EventBus.$off('simulationStepChange')
  }
}
